.btn-container {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
  width: 60%;
  padding: 0;
}

.btn-container .btn {
  height: 80px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: var(--text-pri);
  transition: 0.2s ease;
  overflow: hidden;
}

.btn-container a {
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 18px;
}

.btn-container .btn.pri {
  border: 2px solid var(--color-sec);
}

.btn-container .btn.pri:hover {
  border: none;
  background-color: var(--color-sec);
}

.btn-container .btn.pri::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 0%;
  left: 0;
  transition: 0.2s ease;
}
.btn-container .btn.pri::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 0%;
  left: 0;
  transition: 0.4s ease;
}
.btn-container .btn.pri:hover::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 65%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.274);
}
.btn-container .btn.pri:hover::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.089);
}

.btn-container .btn.sec {
  background-color: var(--color-primary-variant);
}

.btn-container .btn.sec:hover {
  border: 2px solid var(--color-primary);
  background-color: transparent;
}

.btn-container .btn.sec::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 65%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.171);
  transition: 0.2s ease;
}
.btn-container .btn.sec::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 50%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.089);
  transition: 0.4s ease;
}

.btn-container .btn.sec:hover::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 0%;
  left: 0;
}
.btn-container .btn.sec:hover::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 0%;
  left: 0;
}

